@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
.app{height:100vh;overflow-y:scroll;background:url(/heroes-panel/static/media/hero_bg.0540b794.jpg) center/cover no-repeat}.content{display:grid;grid-template-columns:560px 440px;max-width:1040px;margin:0 auto;padding-top:50px}.content__interactive{padding-left:40px;position:relative}.fade-enter{transition:all .5s linear;transform:scale(0, 0)}.fade-enter-active{transition:all .5s linear;transform:scale(1, 1)}.fade-exit{transition:all .5s linear;transform:scale(1, 1)}.fade-exit-active{transition:all .5s linear;transform:scale(0, 0)}
*{margin:0;padding:0;box-sizing:border-box;font-family:"Roboto",sans-serif}ul{padding:0}form{position:-webkit-sticky;position:sticky;top:0}
